.benefit-card{
  background: #FDFDFD;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  padding: 14px 35px 75px;
  &__icon{
    height: 115px;
    display: flex;
    align-items: center;
    justify-content: center;
    img{
      max-height: 100%;
    }
  }
  &__title{
    font-weight: 700;
    font-size: 23.8824px;
    line-height: 29px;
    margin: 13px 0 30px;
    text-align: center;
  }
}

.plan-card{
  padding: 21px 27px;
  background: #FFFFFF;
  border-radius: 10px;
  &__icon{
    height: 88px;
    display: flex;
    justify-content: center;
    img{
      height: 100%;
    }
  }
  &__title{
    text-align: center;
    font-weight: 700;
    font-size: 28px;
    line-height: 34px;
    margin-top: 18px;
  }
  &__items{
    margin-top: 30px;
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    li{
      line-height: inherit;
      display: flex;
      align-items: center;
      &:before{
        content: '';
        background: #5FE72F;
        width: 9px;
        height: 9px;
        margin-right: 11px;
        flex-shrink: 0;
      }
    }
  }
  &__btn{
    margin-top: 25px;
    height: 37.05px;
    width: 100%;
    font-size: 16px;
  }
}

.feedback-card{
  background: #FFFFFF;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(2px);
  border-radius: 15px;
  padding: 35px;
  &__top{
    display: flex;
    align-items: center;
  }
  &__img{
    background: #D9D9D9;
    width: 60px;
    height: 60px;
    padding: 3px;
    border-radius: 50%;
    margin-right: 12px;
    flex-shrink: 0;
    img{
      width: 100%;
      height: 100%;
      border-radius: 50%;
      display: block;
      object-fit: cover;
    }
  }
  &__prof{
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #939393;
    margin-top: 10px;
  }
  &__text{
    margin-top: 19px;
  }
}


@media screen and (max-width: 576px){
  .benefit-card{
    padding: 8px 20px 44px;
    &__icon{
      height: 67px;
    }
    &__title{
      font-size: 14.1197px;
      line-height: 17px;
      margin: 7px 0 17px;
    }
  }

  .plan-card{
    padding: 22px 29px 29px;
    &__icon{
      height: 95px;
    }
    &__title{
      font-size: 30.3112px;
      line-height: 37px;
      margin-top: 19px;
    }
    &__items{
      margin-top: 32px;
      gap: 32px;
      font-size: 17.3207px;
      line-height: 21px;
    }
    &__btn{
      border-radius: 10.8254px;
      height: 40px;
      margin-top: 27px;
    }
  }

  .feedback-card{
    .body-text{
      font-size: 16px;
      line-height: 19px;
    }
  }
}
