.footer{
  margin-top: 60px;
  background: #D9D9D9;
  padding: 42px 0;
  text-align: center;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 18px;
    line-height: 22px;
  }
  &__info{
    line-height: inherit;
    max-width: 435px;
    margin: 0 15px;
    &>*{
      line-height: inherit;
    }
  }
  &__link{
    text-decoration: underline;
  }
}


@media screen and (max-width: 576px){
  .footer{
    margin-top: 25px;
    padding: 19px 0;
    &__inner{
      font-size: 6.89785px;
      line-height: 8px;
    }
    &__info{
      max-width: 170px;
    }
  }
}
