.display1{
  font-weight: 700;
  font-size: 60px;
  line-height: 120%;
}

.display2{
  font-weight: 700;
  font-size: 48px;
  line-height: 123%;
}

.body-text{
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 10px;
    }
  }
}

.gray-text{
  color: #A0A0A0;
}

@media screen and (max-width: 992px){
  .display1{
    font-size: 45px;
  }

  .display2{
    font-size: 32px;
  }
}

@media screen and (max-width: 768px){
  .display1{
    font-size: 30px;
  }
}

@media screen and (max-width: 576px){
  .display1{
    font-size: 17.2278px;
    line-height: 21px;
  }

  .body-text{
    font-size: 9.40527px;
    line-height: 11px;
  }
}
