.contacts{
  padding: 60px 0 130px;
  &__inner{
    background-color: #fff;
  }
  &__title{
    padding: 50px;
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
  }
  &__desc{
    padding: 50px;
    border-top: 1px solid #D9D9D9;
    border-bottom: 1px solid #D9D9D9;
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
  }
  &__item{
    font-size: 20px;
    line-height: 24px;
  }
  &__form{
    padding: 50px;
    width: 470px;
    max-width: 100%;
    display: grid;
    grid-template-columns: 100%;
    gap: 50px;
  }
  &__btn{
    height: 58px;
    background: #6C6ADB;
  }
}


@media screen and (max-width: 576px){
  .contacts{
    padding: 15px 0 45px;
    &__title{
      padding: 15px;
      font-size: 6.99194px;
      line-height: 8px;
    }
    &__desc{
      padding: 15px;
      gap: 5px;
    }
    &__item{
      font-size: 6.5917px;
      line-height: 8px;
    }
    &__form{
      width: 100%;
      padding: 14px 30px 28px;
      gap: 40px;
    }
    &__btn{
      font-size: 16.2161px;
      border-radius: 8.10806px;
      height: 47.03px;
    }
  }
}
