@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    height: 68px;
    font-size: 20px;
    border-radius: 8.33333px;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-primary{
    color: #fff;
    background-color: $primary;
    &:hover{
        color: #fff;
        background-color: darken($primary,10%);
    }
}

.m-btn-primary-outline{
    color: $black;
    border: 1px solid $primary;
    &:hover{
        color: #fff;
        background-color: $primary;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 45px;
        font-size: 17px;
    }
}


@media screen and (max-width: 576px){
    .m-btn{
        height: 30px;
        font-size: 10px;
        border-radius: 3.73353px;
    }
}
