.header{
  padding: 40px 0;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    display: block;
    width: 118px;
    margin-right: 105px;
    img{
      width: 100%;
      display: block;
      filter: drop-shadow(0px 11.7226px 23.4452px rgba(33, 35, 42, 0.25));
    }
  }
  &__action{
    margin-left: auto;
    display: flex;
    align-items: center;
  }
  &__login{
    padding: 0 16px;
  }
  &__start{
    width: 193.33px;
    margin-left: 40px;
  }
}

.header-menu{
  width: 289px;
  color: #fff;
  position: relative;
  border-radius: 8.33333px;
  &__show{
    position: relative;
    z-index: 4;
    width: 100%;
    height: 58px;
    background-color: $primary;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    border-radius: inherit;
    cursor: pointer;
  }
  &__img{
    flex-shrink: 0;
    width: 35px;
    height: 35px;
    margin-right: 10px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 50%;
    }
  }
  &__arrow{
    margin-left: 10px;
    flex-shrink: 0;
    transition: all .3s;
  }
  &__hidden{
    position: absolute;
    z-index: 3;
    top: 0;
    left: 0;
    width: 100%;
    padding: 58px 5px 0;
    background-color: $primary;
    border-radius: inherit;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
  }
  &__link{
    height: 41px;
    display: flex;
    align-items: center;
    padding: 0 12px;
    font-size: 16px;
    line-height: 19px;
    border-top: 1px solid #D9D9D9;
    img{
      flex-shrink: 0;
      margin-right: 13px;
    }
    &:hover{
      color: #fff;
      opacity: .8;
    }
  }
  &__backdrop{
    display: none;
    z-index: 2;
  }
  &._open{
    .header-menu__arrow{
      transform: rotate(180deg);
    }
    .header-menu__hidden{
      opacity: 1;
      pointer-events: unset;
    }
    &~.header-menu__backdrop{
      display: block;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    font-size: 20px;
    line-height: 120%;
    &:not(:last-child){
      margin-right: 48px;
    }
  }
  &__backdrop{
    display: none;
  }
}

.burger{
  display: none;
}

.backdrop{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
}


@media screen and (max-width: 1200px){
  .header{
    &__logo{
      margin-right: 40px;
    }
    &__start{
      margin-left: 25px;
    }
  }

  .nav{
    &__link{
      &:not(:last-child){
        margin-right: 24px;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 20px 0;
    &__inner{
      min-height: 42px;
      position: relative;
      justify-content: space-between;
    }
    &__logo{
      width: 56px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%);
    }
    &__action{
      margin-left: 0;
    }
    &__login{
      padding: 0 8px;
    }
    &__start{
      width: 87px;
      margin-left: 5px;
    }
    &._open{
      .burger{
        transform: translateX(25px);
        span{
          &:first-child{
            transform: rotate(-45deg) translate(-6.5px,6.5px);
          }
          &:nth-child(2){
            transform: scale(0);
          }
          &:nth-child(3){
            transform: rotate(45deg) translate(-6.5px,-6.5px);
          }
        }
      }
      .nav{
        opacity: 1;
        pointer-events: unset;
        &__backdrop{
          display: block;
        }
      }
    }
  }

  .nav{
    z-index: 5;
    position: absolute;
    top: 0;
    left: 0;
    background: #F5F5F5;
    border-radius: 5px;
    flex-direction: column;
    padding: 49px 24px 10px;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__link{
      margin-right: auto;
      &:not(:last-child){
        margin: 0 auto 15px 0;
      }
    }
    &__backdrop{
      z-index: 4;
    }
  }

  .burger{
    position: relative;
    z-index: 6;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 37px;
    height: 37px;
    border-radius: 3.73353px;
    background: #F5F5F5;
    padding: 8px 5px;
    transition: all .3s;
    span{
      display: block;
      height: 2px;
      width: 100%;
      background-color: $black;
      border-radius: 5px;
      &:not(:last-child){
        margin-bottom: 8px;
      }
    }
    &:hover{
      span{
        background-color: #fff;
      }
    }
  }

  .header-menu{
    width: 114.13px;
    border-radius: 3.53549px;
    &__show{
      font-size: 8.48518px;
      line-height: 10px;
      height: 25px;
    }
    &__img{
      width: 14.85px;
      height: 14.85px;
      margin-right: 4.24px;
    }
    &__arrow{
      margin-left: 4.24px;
      width: 8px;
    }
    &__hidden{
      padding: 25px 2px 0;
    }
    &__link{
      height: 18px;
      font-size: 6.78814px;
      line-height: 8px;
      padding: 0 5px;
      img{
        width: 11px;
        margin-right: 5px;
      }
    }
  }
}


@media screen and (max-width: 375px){
  .header{
    &__inner{
      justify-content: space-between;
    }
    &__logo{
      position: static;
      transform: unset;
    }
  }
}
