.privacy{
  padding: 60px 0 80px;
  &__title{
    text-align: center;
    margin-bottom: 100px;
  }
  &__subtitle{
    font-weight: 700;
    font-size: 23px;
    line-height: 32px;
    margin-bottom: 6px;
  }
  &__group{
    font-size: 17px;
    line-height: 24px;
    &:not(:last-child){
      margin-bottom: 50px;
    }
    p,
    ul,
    h4{
      line-height: inherit;
      &:not(:last-child){
        margin-bottom: 20px;
      }
    }
    h4{
      font-weight: 700;
    }
    ul{
      padding-left: 69px;
      display: grid;
      grid-template-columns: 100%;
      gap: 18px;
      p{
        &:not(:last-child){
          margin-bottom: 7px;
        }
      }
    }
    li{
      line-height: inherit;
    }
    b{
      font-weight: 700;
    }
    a{
      font-weight: 700;
      text-decoration: underline;
    }
  }
}

@media screen and (max-width: 576px){
  .privacy{
    padding: 15px 0 75px;
    &__title{
      margin-bottom: 30px;
    }
    &__subtitle{
      font-size: 12.9653px;
      line-height: 18px;
    }
    &__group{
      font-size: 10.1468px;
      line-height: 14px;
      &:not(:last-child){
        margin-bottom: 30px;
      }
      p,
      ul,
      h4{
        &:not(:last-child){
          margin-bottom: 30px;
        }
      }
      ul{
        padding-left: 0;
        gap: 30px;
      }
    }
  }
}
