.m-modal {
    background: rgba(19, 27, 45, 0.52);
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    opacity: 0;
    pointer-events: none;
    &--active {
        opacity: 1;
        pointer-events: unset;
        .m-modal__dialog{
            opacity: 1;
        }
    }
    &__dialog {
        transition: all .3s;
        opacity: 0;
        background: #FFFFFF;
        border-radius: 5px;
        width: 495px;
        max-width: calc(100% - 50px);
        overflow: auto;
        max-height: 100vh;
        padding: 27px;
        position: relative;
    }
    &__title{
        font-weight: 400;
        font-size: 30px;
        line-height: 120%;
    }
    &__form{
        margin-top: 34px;
        .fg{
            &:not(:first-child){
                margin-top: 26px;
            }
        }
    }
    &__forgot{
        font-size: 14px;
        line-height: 17px;
        color: #ACACAD;
        margin-top: 14px;
    }
    &__btn{
        margin-top: 26px;
        width: 100%;
        border-radius: 5px;
        height: 62px;
        font-size: 18px;
    }
    &__subtitle{
        font-size: 16px;
        line-height: 19px;
        margin-top: 34px;
        text-align: center;
    }
    &__socials{
        display: grid;
        grid-template-columns: 1fr 1fr;
        margin-top: 34px;
        gap: 25px;
    }
    &__social{
        border-radius: 6.01648px;
        height: 46.82px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 14px;
        img{
            width: 30px;
            flex-shrink: 0;
            margin-right: 5px;
        }
        &--fb{
            color: #FFFFFF;
            background: #3566A5;
        }
        &--g{
            background: #EDF0F7;
            img{
                margin-right: 15px;
            }
        }
    }
    &__hint{
        font-size: 14px;
        line-height: 17px;
        margin-top: 15px;
        text-align: center;
        a,
        button{
            color: #B0B1B2;
            font-size: inherit;
            line-height: inherit;
        }
    }
    &__or{
        text-align: center;
        margin: 17px 0;
        font-size: 18px;
        line-height: 22px;
    }
}


.m-modal-sub{
    .m-modal{
        &__dialog{
            width: 533px;
        }
        &__title{
            margin-bottom: 52px;
        }
        &__btn{
            margin: 0;
            height: 64px;
            border-radius: 5px;
            font-size: 20px;
        }
    }
}


@media screen and (max-width: 576px){
    .m-modal{
        &__dialog{
            padding: 18px;
            border-radius: 3.48178px;
        }
        &__title{
            font-size: 20.8907px;
            line-height: 25px;
        }
        &__form{
            margin-top: 23px;
            .fg{
                &:not(:first-child){
                    margin-top: 18px;
                }
            }
        }
        &__forgot{
            font-size: 9.74899px;
            line-height: 12px;
            margin-top: 10px;
        }
        &__btn{
            height: 43.48px;
            margin-top: 18px;
            font-size: 12.5344px;
            border-radius: 3.48178px;
        }
        &__subtitle{
            font-size: 11.1417px;
            line-height: 13px;
            margin-top: 23px;
        }
        &__socials{
            margin-top: 23px;
            gap: 17px;
        }
        &__social{
            height: 32.61px;
            font-size: 9.74899px;
            border-radius: 4.18962px;
            img{
                width: 21px;
                margin-right: 3px;
            }
            &--g{
                img{
                    margin-right: 10px;
                }
            }
        }
        &__hint{
            font-size: 10.2521px;
            line-height: 12px;
            margin-top: 11px;
        }
        &__or{
            margin: 11px 0;
            font-size: 11.8199px;
            line-height: 14px;
        }
    }

    .m-modal-sub{
        .m-modal{
            &__title{
                margin-bottom: 33px;
                font-size: 19.6998px;
                line-height: 24px;
            }
            &__btn{
                height: 42.03px;
                font-size: 13.1332px;
                border-radius: 3.2833px;
            }
        }
    }
}
