.fg {
  position: relative;
  margin: 0;
  input,
  textarea{
    border: 1px solid #D9D9D9;
    padding: 0 20px;
    border-radius: 10px;
    height: 58px;
    width: 100%;
    font-size: 16px;
    line-height: 19px;
    background-color: transparent;
    color: #000;
    &::placeholder {
      color: #999999;
    }
    &:focus{
      border-color: $primary;
    }
  }
  textarea{
    height: 134px;
    padding: 20px;
    resize: none;
  }
}

.check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        // background-color: $green;
        border: 3px solid #fff;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.45);
      width: 20px;
      height: 20px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}

.fg2{
  input{
    border-color: #EDF0F7;
    background: #EDF0F7;
    border-radius: 5px;
    padding-left: 56px;
    height: 48.78px;
  }
  img{
    position: absolute;
    left: 14px;
    top: 50%;
    transform: translateY(-50%);
  }
}

@media screen and (max-width: 576px){
  .fg{
    input,
    textarea{
      padding: 16px;
      font-size: 12.9729px;
      line-height: 16px;
      height: 47.03px;
    }
    textarea{
      height: 109px;
    }
  }

  .fg2{
    input{
      font-size: 9.74899px;
      line-height: 12px;
      padding-left: 39px;
      height: 33.97px;
      border-radius: 3.48178px;
    }
    img{
      width: 17px;
      left: 9px;
    }
  }
}
