.main{
  padding: 52px 0;
  overflow: hidden;
  &__inner{
    display: flex;
    justify-content: space-between;
    padding-right: 94px;
  }
  &__content{
    max-width: 440px;
  }
  &__badge{
    display: inline-flex;
    align-items: center;
    padding: 0 14px;
    font-weight: 700;
    font-size: 62px;
    color: $primary;
    background: #DBDBF6;
    border: 2px solid $primary;
    border-radius: 10px;
    height: 82px;
  }
  &__desc{
    margin-top: 36px;
    max-width: 405px;
    font-weight: 600;
    font-size: 28px;
    line-height: 122%;
  }
  &__btn{
    margin-top: 42px;
    width: 100%;
    max-width: 405px;
  }
  &__media{
    width: 461px;
    position: relative;
    &-round{
      background: #EB9FA7;
      border-radius: 50%;
      padding-bottom: 100%;
      position: relative;
    }
  }
  &__item{
    position: absolute;
    background: #F5F5F5;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    backdrop-filter: blur(2px);
    height: 97px;
    border-radius: 10px;
    max-width: 209px;
    display: flex;
    align-items: center;
    text-align: center;
    padding: 0 24px;
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    img{
      width: 48px;
      flex-shrink: 0;
      margin-right: 24px;
      display: block;
    }
    &--medal{
      top: 11%;
      right: -14%;
    }
    &--flag{
      padding-left: 14px;
      bottom: 9%;
      left: -8%;
      img{
        margin-right: 20px;
        width: 69px;
      }
    }
  }
  &__img{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 57.3%;
    height: 57.3%;
    object-fit: cover;
    border-radius: 50%;
  }
  &__camera{
    position: absolute;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    right: -1.3%;
    bottom: 17.1%;
  }
}

.benefits{
  padding: 130px 0;
  margin-top: 50px;
  &__title{
    text-align: center;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 55px;
    margin-top: 100px;
  }
}

.plans{
  padding: 130px 0;
  &__title{
    text-align: center;
  }
  &__grid{
    padding: 0 65px;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 80px;
    margin-top: 95px;
  }
}

.feedback{
  padding: 130px 0;
  &__title{
    text-align: center;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 110px;
    margin-top: 100px;
  }
}


@media screen and (max-width: 1250px){
  .benefits{
    &__grid{
      margin-top: 75px;
      gap: 25px;
    }
  }

  .plans{
    &__grid{
      padding: 0;
    }
  }

  .feedback{
    &__grid{
      gap: 35px;
    }
  }
}


@media screen and (max-width: 1100px){
  .benefits{
    &__grid{
      margin-top: 50px;
      grid-template-columns: repeat(3,1fr);
    }
  }

  .plans{
    &__grid{
      margin-top: 50px;
      gap: 25px;
    }
  }

  .feedback{
    &__grid{
      margin-top: 50px;
      grid-template-columns: repeat(2,1fr);
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    &__inner{
      padding-right: 70px;
    }
    &__content{
      max-width: 370px;
    }
    &__badge{
      height: 60px;
      font-size: 45px;
    }
    &__desc{
      margin-top: 20px;
      font-size: 20px;
      max-width: 320px;
    }
    &__btn{
      max-width: 200px;
      margin-top: 20px;
    }
    &__item{
      padding: 0 18px;
      font-size: 12px;
      line-height: 120%;
      max-width: 150px;
      height: 65px;
      img{
        margin-right: 13px;
        width: 32px;
      }
    }
    &__camera{
      width: 75px;
      height: 75px;
      img{
        width: 45px;
      }
    }
  }

  .benefits{
    padding: 90px 0;
    &__grid{
      grid-template-columns: 1fr 1fr;
      gap: 74px 33px;
    }
  }

  .plans{
    padding: 90px 0;
    &__grid{
      grid-template-columns: 1fr 1fr;
      gap: 40px;
    }
  }

  .feedback{
    padding: 90px 0;
  }
}


@media screen and (max-width: 768px){
  .main{
    padding: 20px 0;
    &__inner{
      padding-right: 35px;
    }
    &__content{
      max-width: 240px;
      margin-right: 20px;
      flex-shrink: 0;
    }
    &__badge{
      font-size: 31px;
      height: 40px;
      border-radius: 5px;
      padding: 0 6px;
    }
    &__desc{
      font-size: 15px;
    }
  }

  .plans,
  .feedback{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    padding: 10px 0;
    &__inner{
      padding-right: 31px;
    }
    &__content{
      max-width: 130px;
    }
    &__badge{
      height: 23.54px;
      padding: 0 4px;
      border-radius: 2.8713px;
      font-size: 17.8021px;
      border-width: 0.57426px;
    }
    &__desc{
      font-size: 8.03964px;
      line-height: 10px;
      margin-top: 10px;
      max-width: 116px;
    }
    &__btn{
      margin-top: 12px;
      max-width: 86px;
    }
    &__item{
      height: 35px;
      padding: 0 9px;
      max-width: 77px;
      font-size: 5.16834px;
      line-height: 6px;
      border-radius: 2.8713px;
      img{
        width: 18px;
        margin-right: 8px;
      }
      &--flag{
        padding-left: 5px;
        max-width: 71px;
        img{
          width: 26px;
          margin-right: 7px;
        }
      }
    }
    &__camera{
      width: 36px;
      height: 36px;
      img{
        width: 22px;
      }
    }
  }

  .benefits{
    padding: 25px 0;
    margin-top: 15px;
    &__grid{
      margin-top: 26px;
    }
  }

  .plans{
    padding: 25px 0;
    &__grid{
      margin-top: 26px;
    }
  }

  .feedback{
    padding: 25px 0;
    &__grid{
      margin-top: 26px;
    }
  }
}
